<template>
    <div class="intro">
        <div class="container">
            <h1>{{ $store.getters.doubleLikes }}</h1>
            <the-green-button @click="$store.commit('incrementLikes')">Лайк</the-green-button>
            <the-green-button @click="$store.commit('decrementLikes')">Дизлайк</the-green-button>
            <h1 class="title  title--white">Страница с постами</h1>
            <the-input
                :model-value="searchQuery"
                @update:model-value="setSearchQuery"
                placeholder="Поиск по названию поста..."
                v-focus
            />
            <div style="display: flex; justify-content: center;">
                <the-green-button @click="showModal">Создать пост</the-green-button>
                <the-select
                    :model-value="selectedSort"
                    @update:model-value="setSelectedSort"
                    :options="sortOptions"
                />
            </div>
            <the-modal v-model:show="modalVisible">
                <post-form @create="createPost" />
            </the-modal>
            <post-list
                :posts="sortedAndSearchedPosts"
                @remove="removePost"
                v-if="!isPostsLoading"
            />
            <h2 class="title  title--white" v-else>Идет загрузка...</h2>
            <div class="observer" v-intersection="loadMorePosts"></div>
        </div>
    </div>
</template>

<script>
    import PostForm from "@/components/PostForm.vue";
    import PostList from "@/components/PostList.vue";
    import TheGreenButton from "@/components/UI/TheGreenButton.vue";
    import { mapState, mapGetters, mapActions, mapMutations } from "vuex";

    export default {
        components: {
            PostForm, PostList, TheGreenButton
        },
    data() {
        return {
            modalVisible: false,
        }
    },
    methods: {
        ...mapMutations({
            setPage: 'post/setPage',
            setSearchQuery: 'post/setSearchQuery',
            setSelectedSort: 'post/setSelectedSort'
        }),
        ...mapActions({
            loadMorePosts: 'post/loadMorePosts',
            fetchPosts: 'post/fetchPosts',
        }),
        createPost(post) {
            this.posts.push(post);
            this.modalVisible = false;
        },
        removePost(post) {
            this.posts = this.posts.filter(p => p.id !== post.id);
        },
        showModal() {
            this.modalVisible = true;
        },
    },
    mounted() {
        this.fetchPosts();
    },
    computed: {
        ...mapState({
            posts: state => state.post.posts,
            isPostsLoading: state => state.post.isPostsLoading,
            selectedSort: state => state.post.selectedSort,
            searchQuery: state => state.post.searchQuery,
            page: state => state.post.page,
            limit: state => state.post.limit,
            totalPages: state => state.post.totalPages,
            sortOptions: state => state.post.sortOptions,
        }),
        ...mapGetters({
            sortedPosts: 'post/sortedPosts',
            sortedAndSearchedPosts: 'post/sortedAndSearchedPosts',
        }),
    }
  }
</script>

<style scoped>
    /* Intro */

    .intro {
        background: url("../assets/bg.jpg") center no-repeat;
        width: 100%;
        height: 100vh;
        background-size: cover;
    }

    /* Pages */

    .pages {
        display: flex;
        padding: 10px 0 20px;
    }

    .pages__items {
        padding: 10px;
        border: 1px solid black;
    }

    .current-page {
        border: 2px solid teal;
    }

    /* Observer */

    .observer {
        height: 30px;
    }
</style>