<template>
    <input
        class="input"
        type="text"
        :value="modelValue"
        @input="updateInput"
    >
</template>

<script>
export default {
    name: 'the-input',
    props: {
        modelValue: [String, Number],
    },
    methods: {
        updateInput(event) {
            this.$emit('update:modelValue', event.target.value);
        }
    }
}
</script>

<style scoped>
    .input {
        width: 100%;
        margin: 20px 0;
        padding: 10px;

        font-family: 'Roboto', sans-serif;

        border: 2px solid #41B883;
    }

    .input::placeholder {
        color: #41B883;
    }

    .input:focus {
        outline: none;
    }
</style>