<template>
    <div
        class="modal"
        v-if="show"
        @click="hideModal"
    >
        <div class="modal__inner">
            <div class="modal__content" @click.stop>
                <slot></slot>
            </div>
        </div>
    </div>
</template>

<script>
import toggleMixin from "@/mixins/toggleMixin";

export default {
    name: 'the-modal',
    mixins: [toggleMixin],
}
</script>

<style scoped>
    .modal {
        background-color: rgba(0, 0, 0, .5);

        position: fixed;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 2000;
    }

    .modal__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 100%;
    }

    .modal__content {
        width: 100%;
        max-width: 500px;
        padding: 0 20px 20px 20px;

        background-color: #fff;
        border-radius: 10px;
    }
</style>