<template>
    <div class="intro">
        <div class="container">
            <the-input
                v-model="searchQuery"
                placeholder="Поиск по названию поста..."
                v-focus
            />
            <div style="display: flex; justify-content: center;">
                <the-green-button @click="showModal">Создать пост</the-green-button>
                <the-select
                    v-model="selectedSort"
                    :options="sortOptions"
                />
            </div>
            <the-modal v-model:show="modalVisible">
                <post-form @create="createPost" />
            </the-modal>
            <post-list
                :posts="sortedAndSearchedPosts"
                @remove="removePost"
                v-if="!isPostsLoading"
            />
            <h2 class="title  title--white" v-else>Идет загрузка...</h2>
            <!-- <div class="observer" v-intersection="loadMorePosts"></div> -->
            <div class="pages">
                <div
                    class="pages__items"
                    :class="{ 'current-page': page === pageNumber }"
                    v-for="pageNumber in totalPages"
                    :key="pageNumber"
                    @click="changePage(pageNumber)"
                >
                    {{ pageNumber }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import PostForm from "@/components/PostForm.vue";
    import PostList from "@/components/PostList.vue";
    import TheGreenButton from "@/components/UI/TheGreenButton.vue";
    import TheSelect from '@/components/UI/TheSelect.vue';
    import axios from "axios";

    export default {
        components: {
            PostForm, PostList, TheGreenButton, TheSelect
        },
    data() {
        return {
            posts: [],
            modalVisible: false,
            isPostsLoading: false,
            selectedSort: '',
            searchQuery: '',
            page: 1,
            limit: 10,
            totalPages: 0,
            sortOptions: [
                { value: 'title', name: 'по названию' },
                { value: 'body', name: 'по описанию' },
            ],
        }
    },
    methods: {
        createPost(post) {
            this.posts.push(post);
            this.modalVisible = false;
        },
        removePost(post) {
            this.posts = this.posts.filter(p => p.id !== post.id);
        },
        showModal() {
            this.modalVisible = true;
        },
        changePage(pageNumber) {
            this.page = pageNumber;
        },
        async fetchPosts() {
            try {
                this.isPostsLoading = true;

                const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        _page: this.page,
                        _limit: this.limit,
                    }
                });
                this.totalPages = Math.ceil(response.headers['x-total-count'] / this.limit);
                this.posts = response.data;
            } catch (event) {
                alert('Ошибка!');
            } finally {
                this.isPostsLoading = false;
            }
        },
        async loadMorePosts() {
            try {
                this.page += 1;

                this.isPostsLoading = true;

                const response = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        _page: this.page,
                        _limit: this.limit,
                    }
                });
                this.totalPages = Math.ceil(response.headers['x-total-count'] / this.limit);
                this.posts = [...this.posts, ...response.data];
            } catch (event) {
                alert('Ошибка!');
            } finally {
                this.isPostsLoading = false;
            }
        }
    },
    mounted() {
        this.fetchPosts();
    },
    computed: {
        sortedPosts() {
            return [...this.posts].sort((post1, post2) => post1[this.selectedSort]?.localeCompare(post2[this.selectedSort]))
        },
        sortedAndSearchedPosts() {
            return this.sortedPosts.filter(post => post.title.toLowerCase().includes(this.searchQuery.toLowerCase()))
        }
    },
    watch: {
        page() {
            this.fetchPosts();
        }
    }
  }
</script>

<style scoped>
    /* Intro */

    .intro {
        background: url("../assets/bg.jpg") center no-repeat;
        width: 100%;
        height: 100%;
        background-size: cover;
    }

    /* Pages */

    .pages {
        display: flex;
        padding: 10px 0 20px;
    }

    .pages__items {
        margin-right: 10px;
        padding: 10px;

        background-color: #fff;
        cursor: pointer;

        transition: box-shadow .2s linear;
    }

    .pages__items:hover {
        box-shadow: 0 2px 5px #000;
    }

    .pages__items:last-child {
        margin-right: 0;
    }

    .current-page {
        background-color: teal;
    }

    /* Observer */

    .observer {
        height: 30px;
    }
</style>