<template>
    <div v-if="posts.length > 0">
        <h2 class="title  title--white">Список постов</h2>
        <transition-group name="user-list">
            <post-item
                v-for="post in posts"
                :key="post.id"
                :post="post"
                @remove="$emit('remove', post)"
            />
        </transition-group>
    </div>
    <h2 class="title  title--red" v-else>Список постов пуст!</h2>
</template>

<script>
import PostItem from './PostItem.vue'

export default {
    components: { PostItem },
    props: {
        posts: {
            type: Array,
            required: true,
        }
    }
}
</script>

<style scoped>
    .user-list-item {
        display: inline-block;
        margin-right: 10px;
    }

    .user-list-enter-active,
    .user-list-leave-active {
        transition: all .5s ease;
    }

    .user-list-enter-from,
    .user-list-leave-to {
        opacity: 0;
        transform: translateX(130px);
    }

    .user-list-move {
        transition: transform .5s ease;
    }
</style>