<template>
    <select
        class="select"
        :value="modelValue"
        @change="changeOption"
    >
        <option disabled value="">Отсортировать посты</option>
        <option
            v-for="option in options"
            :key="option.value"
            :value="option.value"
        >
            {{ option.name }}
        </option>
    </select>
</template>

<script>
export default {
    name: 'the-select',
    props: {
        modelValue: {
            type: String,
        },
        options: {
            type: Array,
            default: () => [],
        },
    },
    methods: {
        changeOption(event) {
            this.$emit('update:modelValue', event.target.value);
        }
    }
}
</script>

<style scoped>
    .select {
        padding: 10px;

        font-family: 'Roboto', sans-serif;

        background-color: #fff;
        border: 2px solid #41B883;
        color: #41B883;
        cursor: pointer;
    }
</style>