<template>
    <h2 class="title">Создание постов</h2>
    <form class="form" @submit.prevent>
        <the-input
            type="text"
            placeholder="Введите название поста..."
            v-model="post.title"
            v-focus
            style="margin: 0 0 10px"
        />
        <the-input
            type="text"
            placeholder="Введите описание поста..."
            v-model="post.body"
            style="margin: 0 0 10px"
        />
        <the-green-button @click="createPost">Создать</the-green-button>
    </form>
</template>

<script>
export default {
    data() {
        return {
            post: {
                title: '',
                body: '',
            }
        }
    },
    methods: {
        createPost() {
            this.post.id = Date.now();
            this.$emit('create', this.post);
            this.post = {
                title: '',
                body: '',
            };
        }
    }
}
</script>

